<template>
  <!--  -->
  <div>
    <!--  -->
    <div
      v-for="(item,index) in sortedMessageHistory"
      :key="index">
      <!-- AI -->
      <div
        v-if="item.dialogue.answer"
        class="items">
        <!-- AI -->
        <div class="tx">
          <img
            :src="require('@/assets/images/chat.png')"
            style="width:40px;height:40px">
        </div>
        <!--  -->
        <div v-if="!phone"
          class="chat_box ">
          <!--  -->
          <div v-highlight
            class="markdown-body"
            v-if="index < chatList.length - 1 "
            v-html="item.dialogue.answer">
          </div>
          <!-- markdown -->
          <div v-else
            class="markdown-body"
            v-highlight
            v-html="obj.output">
          </div>
        </div>
        <!--  -->
        <div v-else
          class="chat_box phone">
          <div v-highlight
            class="markdown-body"
            v-if="index < chatList.length - 1"
            v-html="item.dialogue.answer">
          </div>
          <div v-else
            class="markdown-body"
            v-highlight
            v-html="obj.output">
          </div>
        </div>
      </div>
      <!--  -->
      <div
        v-if="item.dialogue.question"
        class="items items_right">
        <!--  -->
        <div v-if="!phone"
          class="chat_box"
          v-html="item.dialogue.question">
        </div>
        <!--  -->
        <div v-else
          class="chat_box phone"
          v-html="item.dialogue.question">
        </div>
        <!--  -->
        <div class="tx">
          <img
            src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
            style="width:40px;height:40px">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EasyTyper from 'easy-typer-js'
// marked 
import { marked } from 'marked'
// 
import 'highlight.js/styles/devibeans.css'
export default {
  props: ['messageHistory', 'dialogueNoUpdate'],
  data() {
    return {
      phone: false,
      typewriter: '',
      mdRegex: '',
      i: 0,
      timer: 0,
      chatList: [], //
      dialogueNoUpdateFlag: true,
      obj: {  //
        output: '',
        isEnd: false,
        speed: 80,
        singleBack: true,
        sleep: 0,
        type: 'rollback',
        backSpeed: 0,
        sentencePause: true
      }
    }
  },
  computed: {
    sortedMessageHistory() {
      console.log("sortedMessageHistory")
      if(this.chatList.length!=0) {
        this.chatList.sort((a, b) => a.dialogueNo - b.dialogueNo);
        //dialogueNo
        if(this.dialogueNoUpdateFlag) {
          console.log("dialogueNoUpdated")
          // 
          window.localStorage.setItem('dialogueNo', this.chatList[this.chatList.length - 1].dialogueNo);
        } else {
          console.log("dialogueNo not update")
        }
      }
      return this.chatList
    }
  },
  watch: {
    dialogueNoUpdate: {
      handler(val) {
        this.dialogueNoUpdateFlag = val;
      }
    },
    messageHistory: {
      handler(val) {
        this.chatList = val
        if (val.length > 0) {
          //AI
          if (val[val.length - 1].dialogue.answer) {
            if (this.mdRegex.test(val[val.length - 1].dialogue.answer)) {
              this.initTyped(marked(val[val.length - 1].dialogue.answer))
            } else {
              this.initTyped(val[val.length - 1].dialogue.answer)
            }
          }
          //
          for (var i = 0; i < val.length; i++) {
            //AI
            if (this.mdRegex.test(val[i].dialogue.answer)) {
              val[i].dialogue.answer = marked(val[i].dialogue.answer)
            }
            //
            if (this.mdRegex.test(val[i].dialogue.question)) {
              val[i].dialogue.question = marked(val[i].dialogue.question)
            }
          }
        }
      },
      deep: true
    }
  },
  mounted() {
    // 
    this.mdRegex = /[#*`|.png]/
    // 
    this.phone = JSON.parse(window.localStorage.getItem('phone'))
    //
    this.chatList = this.messageHistory
    //
    if (this.chatList && this.chatList.length > 0) {
      //AI
      if (this.chatList[this.chatList.length - 1].dialogue.answer) {
        //
        if (this.mdRegex.test(this.chatList[this.chatList.length - 1].dialogue.answer)) {
          //
          this.initTyped(marked(this.chatList[this.chatList.length - 1].dialogue.answer))
        } else {
          //
          this.initTyped(this.chatList[this.chatList.length - 1].dialogue.answer)
        }
      }
      //
      for (var i = 0; i < this.chatList.length; i++) {
        //AI
        if (this.mdRegex.test(this.chatList[i].dialogue.answer)) {
          this.chatList[i].dialogue.answer = marked(this.chatList[i].dialogue.answer)
        }
        //
        if (this.mdRegex.test(this.chatList[i].dialogue.question)) {
          this.chatList[i].dialogue.question = marked(this.chatList[i].dialogue.question)
          //
          // this.chatList[j].dialogue.question = js_beautify(this.chatList[j].dialogue.question)
        }
      }
    }
  },
  methods: {
    getMessageRole(dialogue) {
      if (dialogue.question) {
        return "question-role";
      } else if (dialogue.answer) {
        return "answer-role";
      }
      return ""; // Return an empty string for unknown message roles
    },
    initTyped(input, fn, hooks) {
      const obj = this.obj
      const typed = new EasyTyper(obj, input, fn, hooks)
    }
  }
}
</script>

